.container{
    width: 100%;
    height: 686px;
    display: flex;
    flex-direction: column;
}
.buttons{
    width: 100%;
}
.resultSection{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-evenly;
}
.board, .explaination{
    width: 50%;
    height: 100%;
}
@media screen and (max-width:900px) {
    .container{
        height: auto;
    }
    .resultSection{
        flex-direction: column;
    }
    .board, .explaination{
        width: 100%;
    }
    .explaination{
        height: 200px;
    }
}