.container{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.inner{
    width: 362px;
    height: 100%;
    background-color: var(--s-dark);
    overflow-y: auto;
    padding: 2rem;
}
.text{
    width: 100%;
    text-align: center;
}
.text h3{
    color: var(--s-light);
    margin-bottom: 2rem;
}
.text h3 span{
    color: var(--p-light);
}
.text div p{
    color: white;
    font-weight: 600;
    margin-bottom: 0.5rem;
}