.container{
    padding: 2rem;
    background-color: var(--p-dark);
    display: flex;
    justify-content: center;
    gap: 1.5rem;
    flex-wrap: wrap;
}
/*Random digit modal styling*/
.randomDigitContainer{
    padding: 1rem 0rem;
}
.randomDigitContainer h1{
    margin-bottom: 1rem;
}
.selections{
    display: flex;
    gap: 2rem;
}
.selectOption{
    cursor: pointer;
    width: 45px;
    height: 45px;
    text-align: center;
    line-height: 45px;
    font-size: 1.5rem;
    background-color: var(--s-dark);
    color: var(--s-light);
    font-weight: 900;
    text-shadow: 0 0 1px rgb(0 0 0 / 20%), 0 1px 0 rgb(0 0 0 / 20%);
    border-radius: 3px;
    transition: all 0.2s linear;
}
.selectOption:hover{
    background-color: var(--p-dark);
    color: var(--p-light);
}
.active{
    border-radius: 50%;
    background-color: var(--p-dark);
    color: var(--p-light);
}
.generate{
    width: 100%;
    padding: 1rem 0rem;
    display: flex;
    align-items: center;
    justify-content: center;
}